<template>
    <view class="map">
        <view class="map-item" @click="handleOpenMap">
            <view class="map-item-icon">
                <i class="w_editor-icon w_editor-icon-weizhi2-copy"></i>
            </view>
            <text class="text">{{data.name}}</text>
        </view>
    </view>
</template>

<script>
    import website from '@/uni_modules/wangjichao-w_editor/config.js'
    export default {
        props: {
            value: {
              type: [String, Number, Object, Array, Boolean],
              default: undefined,
            },
            option: {
                type: Object,
                default: {},
            },
            readOnly: {
                type: Boolean,
                default: false
            },
        },
        data() {
            return {
				data: {},
                show: true,
                opened: false,
                mapContext: {},
            }
        },
        watch: {
            data: {
                deep: true,
                handler(e) {
                    this.$emit('input', e)
                },
            },
            value: {
                deep: true,
                immediate: true,
                handler(e) {
                    this.data = e
                },
            },
        },
        mounted() {
            uni.$on('chooseLocation',(data)=>{
                const temp = data || {}
                // #ifdef MP-WEIXIN
                    temp.location = [temp.longitude, temp.latitude].join()
                // #endif
                this.data = temp
                this.$emit('permit', !!data)
            })
            if(!this.readOnly && !this.value){
                this.useLocationMap()
            }
        },
        created() {
            // 移除监听事件  
            uni.$off('chooseLocation'); 
        },
        methods: {
            handleOpenMap() {
                let params = {}
                // #ifdef APP-PLUS
                    params = {
                        location: this.data.location,
                        name: this.data.name
                    }
                // #endif
                // #ifdef MP-WEIXIN
                    const endPoint = JSON.stringify({
                        name: this.data.name,
                        latitude: this.data.location.split(',')[1],
                        longitude: this.data.location.split(',')[0]
                    });
                    params = {
                        endPoint
                    }
                // #endif
                this.useLocationMap(params)
            },
            useLocationMap(p) {
                // #ifdef APP-PLUS
                    const params = this.objectToUrlParams(p)
                    uni.navigateTo({
                        url: "/uni_modules/wangjichao-w_editor/components/items/map/location" + params
                    })
                // #endif
                // #ifdef MP-WEIXIN
                    const key = website.qqmapKey; //使用在腾讯位置服务申请的key
                    const referer = website.referer; //调用插件的app的名称
                    const temp = Object.assign({
                        key,
                        referer
                    }, p)
                    const params = this.objectToUrlParams(temp)
                    if(!p){
                        wx.navigateTo({
                          url: 'plugin://chooseLocation/index' + params
                        });
                    }else{
                        wx.navigateTo({
                            url: 'plugin://routePlan/index' + params
                        });
                    }
                // #endif
            },
            // obj
            objectToUrlParams(obj = {}) {
                if(typeof obj === 'object'){
                    return Object.entries(obj).reduce((a, b) => {
                        return `${a}${b[0]}=${encodeURIComponent(b[1])}&`
                    }, Object.keys(obj).length>0 ? '?' : '')
                }
                return ''
            },
        }
    }
</script>
