                background-color: #3c9cff;
                display: flex;
                justify-content: center;
                align-items: center;
                .w_editor-icon{
                    font-size: 60rpx;
                    color: #FFF;
                }
            }
            .text{
                color: #666;
                margin-left: 16rpx;
                font-size: 30rpx;
            }
        }
    }
</style>